export default {
  name: 'Footer',
  props: {
    promo_language_code: {
      type: String,
      required: true,
    },
    promo_country_code: {
      type: String,
      required: true,
    },
    landing_page_type: {
      type: String,
      required: false,
    }
  },
  computed: {
    responsibleGamingURL: function () {
      let returnURL = ''
      switch (this.landing_page_type) {
        case 'verajohn':
          returnURL = 'https://www.verajohn.com/about/responsible-gaming';
          break;
        case 'intercasino':
          returnURL = 'https://www.intercasino.com/about/responsible-gaming';
          break;
        default:
          returnURL = '#';
          break;
      }
      return returnURL;
    }
  }
};
